'use client';

import React, { useEffect, useState } from 'react';
import { ShoppingCart, DollarSign, Package, TrendingUp, Clock, CheckCircle } from 'lucide-react';

interface DashboardStats {
    orders: {
        today: number;
        pending: number;
        total: number;
    };
    revenue: {
        today: number;
        total: number;
    };
    notifications: {
        byStatus: Record<string, number>;
        dlqCount: number;
    };
    recentOrders: any[];
}

export default function AdminDashboardPage() {
    const [stats, setStats] = useState<DashboardStats | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        fetchDashboard();
    }, []);

    const fetchDashboard = async () => {
        try {
            const token = localStorage.getItem('admin_token');
            const response = await fetch('http://localhost:3333/api/admin/dashboard', {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'X-Admin-Id': JSON.parse(localStorage.getItem('admin_data') || '{}').id
                }
            });
            const data = await response.json();
            setStats(data);
        } catch (error) {
            console.error('Failed to fetch dashboard:', error);
        } finally {
            setIsLoading(false);
        }
    };

    if (isLoading) {
        return (
            <div className="flex items-center justify-center h-64">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-black"></div>
            </div>
        );
    }

    const statCards = [
        {
            label: 'Commandes Aujourd\'hui',
            value: stats?.orders.today || 0,
            icon: ShoppingCart,
            color: 'bg-blue-500'
        },
        {
            label: 'En Attente',
            value: stats?.orders.pending || 0,
            icon: Clock,
            color: 'bg-orange-500'
        },
        {
            label: 'Revenus Aujourd\'hui',
            value: `${(stats?.revenue.today || 0).toLocaleString()} F`,
            icon: DollarSign,
            color: 'bg-green-500'
        },
        {
            label: 'Revenus Total',
            value: `${(stats?.revenue.total || 0).toLocaleString()} F`,
            icon: TrendingUp,
            color: 'bg-purple-500'
        }
    ];

    return (
        <div className="space-y-6">
            {/* Page Header */}
            <div>
                <h1 className="text-2xl font-bold text-gray-900">Tableau de Bord</h1>
                <p className="text-gray-500">Vue d'ensemble de votre activité</p>
            </div>

            {/* Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {statCards.map((card, index) => {
                    const Icon = card.icon;
                    return (
                        <div key={index} className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6">
                            <div className="flex items-center justify-between mb-4">
                                <div className={`${card.color} p-3 rounded-xl`}>
                                    <Icon className="w-6 h-6 text-white" />
                                </div>
                            </div>
                            <p className="text-2xl font-bold text-gray-900">{card.value}</p>
                            <p className="text-sm text-gray-500 mt-1">{card.label}</p>
                        </div>
                    );
                })}
            </div>

            {/* Recent Orders */}
            <div className="bg-white rounded-2xl shadow-sm border border-gray-100 overflow-hidden">
                <div className="px-6 py-4 border-b border-gray-100">
                    <h2 className="text-lg font-bold text-gray-900">Commandes Récentes</h2>
                </div>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead className="bg-gray-50">
                            <tr>
                                <th className="px-6 py-3 text-left text-xs font-semibold text-gray-500 uppercase">ID</th>
                                <th className="px-6 py-3 text-left text-xs font-semibold text-gray-500 uppercase">Client</th>
                                <th className="px-6 py-3 text-left text-xs font-semibold text-gray-500 uppercase">Total</th>
                                <th className="px-6 py-3 text-left text-xs font-semibold text-gray-500 uppercase">Statut</th>
                                <th className="px-6 py-3 text-left text-xs font-semibold text-gray-500 uppercase">Date</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100">
                            {stats?.recentOrders?.length ? (
                                stats.recentOrders.map((order) => (
                                    <tr key={order.id} className="hover:bg-gray-50">
                                        <td className="px-6 py-4 text-sm font-mono text-gray-900">
                                            {order.id.substring(0, 8)}...
                                        </td>
                                        <td className="px-6 py-4">
                                            <p className="text-sm font-medium text-gray-900">{order.customer_name}</p>
                                            <p className="text-xs text-gray-500">{order.customer_phone}</p>
                                        </td>
                                        <td className="px-6 py-4 text-sm font-semibold text-gray-900">
                                            {order.total?.toLocaleString()} F
                                        </td>
                                        <td className="px-6 py-4">
                                            <div className="flex flex-col gap-1">
                                                <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${order.status === 'completed' ? 'bg-green-100 text-green-800' :
                                                    order.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                                                        'bg-gray-100 text-gray-800'
                                                    }`}>
                                                    {order.status}
                                                </span>
                                                {order.trust_score !== null && order.trust_score !== undefined && (
                                                    <span className={`text-[10px] font-bold px-2 py-0.5 rounded border ${order.trust_score >= 80 ? 'bg-green-50 text-green-600 border-green-200' :
                                                        order.trust_score >= 50 ? 'bg-orange-50 text-orange-600 border-orange-200' :
                                                            'bg-red-50 text-red-600 border-red-200'
                                                        }`}>
                                                        Confiance: {order.trust_score}%
                                                    </span>
                                                )}
                                            </div>
                                        </td>
                                        <td className="px-6 py-4 text-sm text-gray-500">
                                            {new Date(order.created_at).toLocaleDateString('fr-FR')}
                                        </td>
                                    </tr>
                                ))
                            ) : (
                                <tr>
                                    <td colSpan={5} className="px-6 py-12 text-center text-gray-500">
                                        Aucune commande récente
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </div>

            {/* DLQ Alert */}
            {(stats?.notifications?.dlqCount ?? 0) > 0 && stats && (
                <div className="bg-red-50 border border-red-200 rounded-xl p-4 flex items-center gap-4">
                    <div className="bg-red-500 p-2 rounded-lg">
                        <Package className="w-5 h-5 text-white" />
                    </div>
                    <div>
                        <p className="font-semibold text-red-800">
                            {stats.notifications.dlqCount} notification(s) en échec
                        </p>
                        <p className="text-sm text-red-600">
                            Consultez la file d'attente des erreurs pour résoudre les problèmes.
                        </p>
                    </div>
                </div>
            )}
        </div>
    );
}
